<template>
	<div class="page" >

		<RateEditor :participant-id="participants.id" :readOnly='false'></RateEditor>

		<div id="rate-card">

			<div id="rate-panel">

				<div class="content">

					<div class="weui-flex">

						<div class="weui-flex__item">

							<div class="content-flag">
								<div class="logo active">
									写
								</div>
							</div>
						</div>

						<div v-show="!commit" class="weui-flex__item content-info" @click="save()">暂存</div>
						<div v-show="!commit" class="weui-flex__item content-oprations canSubmit"  
						@click="openConfirm()">提交文章</div>

					</div>

				</div>
			</div>
		</div>
	
		<div class="v-modal" v-show="realCommit" style="z-index: 2000;"></div>
	</div>

</template>

<script>

	import RateEditor from './RateEditor';
	import server from '../assets/js/write.js';
	import {MessageBox} from 'mint-ui';

	export default {
		name:'meetingrate',
		
		data() {
			return {
				participants:{
	 				id:this.$route.query.id,
	 				meetingmsg:''
	 			},
	 			wordslimit:this.$route.query.wordslimit,
	 			seen:false,
	 			label:'提交文章',
	 			commit:false,
	 			realCommit: false,
			}
		},

		mounted(){
			 var this2 = this;
			 server.getthinking(this.participants.id).then(function(response) {	
	         var logo = response.data.code;	
	         if (logo == -4) {
	         	this2.label = '已经提交';
	         	this2.commit = true;
	         }else{

	         }        	 
	         }, function(response) {
	            console.log(response);
	         });
		},
		methods: {
			save(){
				var text = document.getElementsByClassName('ql-editor')[0].innerHTML.replace(/\s/g,'&nbsp;');
	   			this.participants.meetingmsg = text;
	   			//console.log(this.participants);
	 	        server.save(this.participants).then(function(response) {
	 	        	console.log(response.data);
	  				var data = response.data.code;
	  				if (data == 2) {
	  					alert("暂存成功")
	  				}else if (data == -2) {
	  					alert("您已超过提交期限！")
	  				}else{
	  					alert("请求超时！")
	  				}
	 	            
	 	        }).catch(function (error) {
	           		console.log(error);
	        	});		       
	   		},

	   		commitdata(){

	   			if(this.commit != true){

	   				var text = document.getElementsByClassName('ql-editor')[0].innerHTML.replace(/\s/g,'&nbsp;');
	   				this.participants.meetingmsg = text;
	   			let this2 = this;
	  			server.commit(this.participants).then(function(response) {
	  				console.log(response.data.code);
	  				var data = response.data.code;
	  				if (data == 1) {
	  					this2.commit = true;
	  					alert("提交成功");
	  				}else if (data == -2) {
	  					alert("您已超过提交期限")
	  				}else if (data == -3) {
	  					alert("您已提交过心得，不得重复提交")	  					
	  				}else{
	  					alert("请求超时！")
	  				}		           	
		            
		        }).catch(function (error) {
	          		console.log(error);
	        	});
	        	
	   			}

	  		},

	  		openConfirm() {

	  			this.realCommit = true;
	  			//console.log(this.$store.state.user.wordscount);
	  			//console.log(this.wordslimit);
	  			//console.log(this.$store.state.user.wordslineBreak); //是否超过7行

	  			if(this.$store.state.user.wordscount > this.wordslimit){
	  				this.realCommit = false;
	  				MessageBox.alert('超过上限【'+this.wordslimit+'】字数，无法提交');
	  			}

	  			else if(this.$store.state.user.wordslineBreak){
	  				this.realCommit = false;
	  				MessageBox.alert('有段落超过7行，无法提交');
	  			}

	  			else{

	  				var this2 = this;

		        	MessageBox.confirm('警告：提交后将无法修改,请确保提交内容符合您的预期!', '确认提交!').then(function(confirm){
		            	this2.commitdata();
		            	this2.realCommit = false;
		        	}).catch(function(cancel){
		           		this2.realCommit = false;
		        	});   

	  			}
		    
		     	
			},

		},

		components: {
			RateEditor,
		},

	}
</script>
<style></style>


